.contact-request-group {
  width: 100%;
  @include fieldset-fix;
  padding-top: 32px;
  padding-bottom: 41px;
  @media (max-width: $tablet-width) {
    padding-left: 20px;
    padding-right: 20px;
  }
  @media (min-width: $tablet-width) and (max-width: $desktop-width) {
    padding-bottom: 27px;
    padding-top: 35px;
  }
  @media (min-width: $tablet-width) {
    border: 2px solid var(--grey);
    margin-bottom: 59px;
    order: 1;
  }
  @media (min-width: $desktop-width) {
    padding-bottom: 0;
    margin-bottom: 56px;
  }
}

.contact-request-group__request-title {
  display: none;
  @media (min-width: $tablet-width) {
    display: inline;
  }
}

.contact-request-group__request-list {
  @include list-fix;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  @media (min-width: $tablet-width) and (max-width: $desktop-width) {
    align-items: center;
  }
  @media (min-width: $desktop-width) {
    flex-direction: row;
    padding-left: 57px;
    padding-right: 59px;
  }
}

.contact-request-group__request-item {
  width: 100%;
  margin-bottom: 16px;
  &:last-child {
    margin-bottom: 0;
  }
  @media (min-width: $tablet-width) and (max-width: $desktop-width) {
    width: 81%;
    margin-bottom: 29px;
  }
  @media (min-width: $desktop-width) {
    max-width: 375px;
  }
}

.contact-request-group__request-container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  @media (min-width: $tablet-width) {
    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 12.5%;
      height: 62%;
      background-color: var(--grey);
      background-repeat: no-repeat;
      background-position: center;
      @media (min-width: $desktop-width) {
        width: 15.5%;
      }
    }
