    }
  }
}

.contact-request-group__question {
  @media (max-width: $tablet-width) {
    margin-bottom: 5px;
  }
  @media (min-width: $tablet-width) {
    order: 1;
    margin-top: 9px;
    margin-left: 18px;
  }
  @media (min-width: $desktop-width) {
    margin-left: 20px;
  }
}

.contact-request-group__request {
  word-spacing: 4px;
  @media (min-width: $tablet-width) {
    width: 88%;
  }
  @media (min-width: $desktop-width) {
    width: 85%;
    padding-left: 22px;
  }
}
